<template>
  <div id="wrap">
    <div class="select" @click="openPopup('areaSlots')">
      <mt-cell title="所属大区" is-link>
        <span>{{area?area: '请选择大区'}}</span>
      </mt-cell>
    </div>
    <div class="select" @click="openPopup('city')">
      <mt-cell title="上车城市" is-link>
        <span>{{areaCity?areaCity: '请选择城市'}}</span>
      </mt-cell>
    </div>
    <div class="select" @click="openPopup('brand')">
      <mt-cell title="车辆品牌" is-link>
        <span>{{brand?brand: '请选择车辆品牌'}}</span>
      </mt-cell>
    </div>
    <div class="select" @click="openPopup('model')">
      <mt-cell title="车辆型号" is-link>
        <span>{{model?model: '请选择车辆型号'}}</span>
      </mt-cell>
    </div>
    <div class="select" @click="openPopup('style')">
      <mt-cell title="车辆款式" is-link>
        <span>{{style?style: '请选择车辆款式'}}</span>
      </mt-cell>
    </div>
    <div class="select" @click="openPopup('carYear')">
      <mt-cell title="车辆年份" is-link>
        <span>{{carYear?carYear: '请选择车辆年份'}}</span>
      </mt-cell>
    </div>
    <div class="select" @click="openPopup('priceMode')">
      <mt-cell title="报价方式" is-link>
        <span>{{priceMode?priceMode: '请选择报价方式'}}</span>
      </mt-cell>
    </div>
    <div>
      <mt-cell title="车辆公告号">
        <span> {{carNumber}}</span>
      </mt-cell>
    </div>
    <div @click="routeLink('./addService')">
      <mt-cell title="增值服务" is-link>
        <span>{{giftType}}
          <span style="color:red">{{giftTypeMoney}}</span>
          <span>{{giftUnit}}</span>
        </span>
      </mt-cell>
    </div>
    <div style="margin-bottom:45px;"  @click="routeLink('./vehicleTechnical')">
      <mt-cell title="车辆技改" is-link>
        <span>
          <span style="color:red">{{carChangeMoney}}</span>
          <span>{{carChangeUnit}}</span>
        </span>
      </mt-cell>
    </div>
    <mt-button class="nextBtn" type="primary" @click="locationRenting()">下一步</mt-button>
    <mt-popup v-model="popupVisible" position="bottom" class="mint-popup">
      <mt-picker :slots="slots" show-toolbar ref="areaPicker">
        <mt-button @click="handleCancel">取消</mt-button>
        <mt-button @click="handleConfirm">确认</mt-button>
      </mt-picker>
    </mt-popup>
  </div>
</template>
<script>
export default {
  data() {
    return {
      popupVisible: false,
      area: '', // 所属大区
      areaCity: '', // 上车城市
      brand: '', // 车辆品牌
      model: '', // 车辆款式
      style: '', // 车辆型号
      carYear: '', // 车辆年份
      priceMode: '', // 报价方式
      giftType: '', // 增值服务礼包类型
      giftTypeMoney: '', // 增值服务价格
      giftUnit: '', // 增值服务单位
      carNumber: '', // 车架号
      carChangeMoney: '', // 车辆技改金额
      carChangeUnit: '', // 车辆技改单位
      slots: [],
      type: '',
      areaSlots: [{
        values: ['华南大区', '华东大区', '华北大区', '华西大区', '华中大区']
      }],
      priceModeSlots: [{
        values: ['直租', '短租', '以租代售', '整车销售']
      }],
      citySlots: [],
      brandSlots: [],
      modelSlots: [],
      styleSlots: [],
      carYearSlots: [],
      cityData: {}, // 城市data
      brandData: {}, // 车辆品牌Data
      modelDate: {}, // 车辆款式
      styleDate: {}, // 车辆型号
      carYearDate: {}, // 车辆年份
      carInfo: {}, // 车辆信息
      priceModeDate: {}, // 报价方式
      renting: {
        '直租': 'longRenting',
        '短租': 'shotRenting',
        '以租代售': 'rentedAndSale',
        '整车销售': 'fullCarSale'
      }
    }
  },
  beforeRouteEnter(to, from, next) {
    next(vm => {
      document.title = '在线报价'
      if (vm.$store.getters['GET_SERVICE_INFO'].price) {
        vm.giftUnit = vm.$store.getters['GET_SERVICE_INFO'].unit
        vm.giftType = vm.$store.getters['GET_SERVICE_INFO'].name
        vm.giftTypeMoney = vm.$store.getters['GET_SERVICE_INFO'].price
      }
      if (vm.$store.getters['GET_VEHICLE_INFO'].price) {
        vm.carChangeMoney = vm.$store.getters['GET_VEHICLE_INFO'].price
        vm.carChangeUnit = vm.$store.getters['GET_VEHICLE_INFO'].unit
      }
    })
  },
  methods: {
    // 跳转
    routeLink(url) {
      if (!this.priceMode) {
        this.$toast('请先选择报价方式')
        return
      }
      this.$router.push(url)
    },
    openPopup(type) {
      this.type = type
      switch (type) {
        case 'areaSlots':
          this.slots = this.areaSlots
          this.popupVisible = true
          break
        case 'city':
          if (this.citySlots.length === 0) {
            this.$toast('请先选择大区')
            return
          }
          this.slots = this.citySlots
          this.popupVisible = true
          break
        case 'brand':
          if (this.brandSlots.length === 0) {
            this.$toast('请先选择城市')
            return
          }
          this.slots = this.brandSlots
          this.popupVisible = true
          break
        case 'model':
          if (this.modelSlots.length === 0) {
            this.$toast('请先选择车辆品牌')
            return
          }
          this.slots = this.modelSlots
          this.popupVisible = true
          break
        case 'style':
          if (this.styleSlots.length === 0) {
            this.$toast('请先选择车辆型号')
            return
          }
          this.slots = this.styleSlots
          this.popupVisible = true
          break
        case 'carYear':
          if (this.carYearSlots.length === 0) {
            this.$toast('请先选择车辆款式')
            return
          }
          this.slots = this.carYearSlots
          this.popupVisible = true
          break
        case 'priceMode':
          if (!this.carYear) {
            this.$toast('请先选择车辆年份')
            return
          }
          this.slots = this.priceModeSlots
          this.popupVisible = true
          break
      }
    },
    handleCancel() {
      this.popupVisible = false
    },
    handleConfirm() {
      var that = this
      switch (this.type) {
        case 'areaSlots':
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-city-list',
            method: 'post',
            data: {
              area: this.$refs.areaPicker.getValues()[0]
            },
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              let areaArr = []
              let areaObj = {};
              let citySlotsArr = [];
              if (res.data.data && res.data.data.length) {
                res.data.data.forEach(function (v, i) {
                  areaArr.push(v.city)
                })
              }
              areaObj.values = areaArr
              citySlotsArr.push(areaObj)
              that.citySlots = citySlotsArr
              that.area = this.$refs.areaPicker.getValues()[0]
              that.areaCity = ''
              that.brand = ''
              that.model = ''
              that.style = ''
              that.carYear = ''
              that.priceMode = ''
              that.carNumber = ''
              that.giftType = ''
              that.giftTypeMoney = ''
              that.giftUnit = ''
              that.initVehicle
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = false
          }).catch(err => {
            console.log(err)
          })
          break
        case 'city':
          this.cityData.area = this.area
          this.cityData.city = this.$refs.areaPicker.getValues()[0]
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-car-brand-list',
            method: 'post',
            data: that.cityData,
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              let areaArr = []
              let areaObj = {};
              let citySlotsArr = [];
              if (res.data.data && res.data.data.length) {
                res.data.data.forEach(function (v, i) {
                  areaArr.push(v.car_brand)
                })
              }
              areaObj.values = areaArr
              citySlotsArr.push(areaObj)
              that.brandSlots = citySlotsArr
              that.areaCity = this.$refs.areaPicker.getValues()[0]
              that.brand = ''
              that.model = ''
              that.style = ''
              that.carYear = ''
              that.priceMode = ''
              that.carNumber = ''
              that.giftType = ''
              that.giftTypeMoney = ''
              that.giftUnit = ''
              that.initVehicle
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = false
          })
          break
        case 'brand':
          this.brandData.area = this.area
          this.brandData.city = this.areaCity
          this.brandData.car_brand = this.$refs.areaPicker.getValues()[0]
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-car-model-list',
            method: 'post',
            data: that.brandData,
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              let areaArr = []
              let areaObj = {};
              let citySlotsArr = [];
              if (res.data.data && res.data.data.length) {
                res.data.data.forEach(function (v, i) {
                  areaArr.push(v.car_model)
                })
              }
              areaObj.values = areaArr
              citySlotsArr.push(areaObj)
              that.modelSlots = citySlotsArr
              that.brand = this.$refs.areaPicker.getValues()[0]
              that.model = ''
              that.style = ''
              that.carYear = ''
              that.priceMode = ''
              that.carNumber = ''
              that.giftType = ''
              that.initVehicle
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = false
          })
          break
        case 'model':
          this.modelDate.area = this.area
          this.modelDate.city = this.areaCity
          this.modelDate.car_brand = this.brand
          this.modelDate.car_model = this.$refs.areaPicker.getValues()[0]
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-car-style-list',
            method: 'post',
            data: that.modelDate,
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              let areaArr = []
              let areaObj = {};
              let citySlotsArr = [];
              if (res.data.data && res.data.data.length) {
                res.data.data.forEach(function (v, i) {
                  areaArr.push(v.car_style)
                })
              }
              areaObj.values = areaArr
              citySlotsArr.push(areaObj)
              that.styleSlots = citySlotsArr
              that.model = this.$refs.areaPicker.getValues()[0]
              that.style = ''
              that.carYear = ''
              that.priceMode = ''
              that.carNumber = ''
              that.giftType = ''
              that.giftTypeMoney = ''
              that.giftUnit = ''
              that.initVehicle
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = false
          })
          break
        case 'style':
          this.styleDate.area = this.area
          this.styleDate.city = this.areaCity
          this.styleDate.car_brand = this.brand
          this.styleDate.car_model = this.model
          this.styleDate.car_style = this.$refs.areaPicker.getValues()[0]
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-car-year-list',
            method: 'post',
            data: that.styleDate,
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              let areaArr = []
              let areaObj = {};
              let citySlotsArr = [];
              if (res.data.data && res.data.data.length) {
                res.data.data.forEach(function (v, i) {
                  areaArr.push(v.car_year)
                })
              }
              areaObj.values = areaArr
              citySlotsArr.push(areaObj)
              that.carYearSlots = citySlotsArr
              that.style = this.$refs.areaPicker.getValues()[0]
              that.carYear = ''
              that.priceMode = ''
              that.carNumber = ''
              that.giftType = ''
              that.giftTypeMoney = ''
              that.giftUnit = ''
              that.initVehicle
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = false
          })
          break
        case 'carYear':
          this.carYearDate.area = this.area
          this.carYearDate.city = this.areaCity
          this.carYearDate.car_brand = this.brand
          this.carYearDate.car_model = this.model
          this.carYearDate.car_style = this.style
          this.carYearDate.car_year = this.$refs.areaPicker.getValues()[0]
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-car-info',
            method: 'post',
            data: that.carYearDate,
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              that.carYear = this.$refs.areaPicker.getValues()[0]
              that.carNumber = res.data.data[0].car_notice_number
              that.carInfo = res.data.data[0]
              this.$store.commit('SET_CAR_INFO', that.carInfo)
              that.priceMode = ''
              that.giftType = ''
              that.giftTypeMoney = ''
              that.giftUnit = ''
              that.initVehicle
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = false
          })
          break
        case 'priceMode':
          this.priceModeDate.price_mode = this.$refs.areaPicker.getValues()[0]
          this.priceModeDate.car_type = that.carInfo.car_type
          this.$axios({
            url: this.$baseUrl + '?r=interfaces/car-valuation/get-service-data',
            method: 'post',
            data: that.priceModeDate,
            transformRequest: [function (data) {
              var ret = '';
              for (var it in data) {
                ret += encodeURIComponent(it) + '=' + encodeURIComponent(data[it]) + '&'
              }
              return ret
            }],
            headers: {
              'Content-Type': 'application/x-www-form-urlencoded'
            }
          }).then(res => {
            if (res.data.errcode === 0) {
              that.giftType = res.data.data[0].name
              that.giftTypeMoney = res.data.data[0].price
              that.giftUnit = res.data.data[0].unit
              res.data.data[0].choosePriceMode = that.$refs.areaPicker.getValues()[0]
              that.priceMode = that.$refs.areaPicker.getValues()[0]
              that.$store.commit('SET_SERVICE_INFO', res.data.data[0])
            } else {
              this.$toast(res.data.msg)
            }
            this.popupVisible = false
          })
          break
      }
    },
    locationRenting() {
      if (!this.priceMode) {
        this.$toast('请选择报价方式')
      } else {
        this.$router.push('./' + this.renting[this.priceMode])
      }
    },
    initVehicle() {
      if (this.$store.getters['GET_VEHICLE_INFO'].price) {
        this.giftTypeMoney = this.$store.getters['GET_VEHICLE_INFO'].price = ''
        this.giftUnit = this.$store.getters['GET_VEHICLE_INFO'].unit = ''
      } else {
        this.giftTypeMoney = ''
        this.giftUnit = ''
      }
    }
  }
}

</script>
<style scoped>
.mint-popup {
  width: 100%;
}

.mint-cell {
  font-size: 12px;
  min-height: 40px;
}

.nextBtn {
  position: fixed;
  bottom: 0;
  width: 100%;
  padding: 0;
  margin-left: -8px;
  border-radius: 0;
  height: 40px;
  line-height: 40px;
}
</style>



